import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import NewsCard from '../NewsCard/NewsCard';
import TextCard from '../TextCard/TextCard';

const NewsSection = ({newsData}) => {
    const TextCardData = {
        iconClass: "fa fa-quote-left fa-5x",
        head: " Why FirstWood ? ",
        text: "In First Wood we guarantee  you the quality of the wood even after it's processed and painted because we manufacture the veneer in our factories using the highest technologies to control the quality and the appearance. First Wood is the best choice for those who are looking for super quality at good prices"
    }
    const {iconClass, head, text} = TextCardData;

    const newsCardsList = newsData.map( ({ id, ...otherNewsProps }) => {
        return(
            <Col xl={3} lg={4} key={id}>
                <NewsCard {...otherNewsProps} />
            </Col>
        )
    })
    return (
        <Container className='magazines-menu my-5'>
            <div style={{marginTop: "100px", marginBottom: "80px"}}>
                <h2>Our Magazines</h2>
            </div>
            <Row>
                {newsCardsList}
                <Col xl={3} lg={4}>
                    <TextCard head={head} iconClass={iconClass} text={text} />
                </Col>
            </Row>
        </Container>
    )
}

export default NewsSection;

