import React from 'react';
import {Card} from 'react-bootstrap';

const FacebookCard = () => (
    <Card className='text-center'>
        <a href="https://www.facebook.com/FirstWoodEG" target="_blank" rel="noopener noreferrer">
            <Card.Body className='d-flex flex-column justify-content-center align-items-center'>
                    <div>
                        <i className="fa fa-facebook" style={{fontSize: "100px"}}/>
                    </div>
                    <div className="facebook-body mt-3">
                        <p className='h3'style={{fontWeight:"800"}}>FOLLOWERS</p>
                        <p className='h3 d-inline' style={{fontWeight:"800"}}>22745 &nbsp;<i className="fa fa-thumbs-o-up"/></p>
                    </div>
            </Card.Body>
        </a>
    </Card>
);
export default FacebookCard;