import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import ProductCard from '../ProductCard/ProductCard';

const ProductsSection = ({productsData}) => {
    const productsList = productsData.map( ({ id, ...otherProductProps }) => {
        return(
            <Col key={id} lg={4} md={6} className='mb-4 card-item'>
                <ProductCard  {...otherProductProps} />
            </Col>
        )
    })
    return (
        <Container className='product-menu my-5'>
            <Row>
                {productsList}
            </Row>
        </Container>
    );
}
export default ProductsSection;