import React from 'react';
import {NavLink} from 'react-router-dom';
import { Container, Navbar, Nav } from 'react-bootstrap';
import Logo from '../../Images/Logo.png';
import {NavLinksData} from './headerData';

class Header extends React.Component{
    constructor(){
        super();
        this.state = {
            navLinks : NavLinksData
        }
    }
    
    render(){
        const {navLinks} = this.state;
        const navLinksList = navLinks.map(({id, path, title}) => {
            return(
                <Nav.Link key={id}  className='mx-2' as={'span'}>
                    <NavLink className="main-hover" to={path}>{title}</NavLink>
                </Nav.Link>
            )
        })
        return(
            <header>
                <Container>
                    <Navbar expand="md" className='my-2'>
                        <NavLink to="/first-wood" className="main-hover">
                            <img src={Logo} width="90px" alt="woodLogo"/>
                        </NavLink>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="ml-auto">
                                {navLinksList}
                            </Nav>
                        </Navbar.Collapse>
                    </Navbar>
                </Container>
            </header>
        )
    }
}
export default Header;