import React from 'react';
import {FormData} from './formData';
import {Container, Row, Col, Form, Button} from 'react-bootstrap';
import './FormSection.scss';

class FormSection extends React.Component{
    constructor() {
        super();
        this.state = {
            contactInfo : FormData
        }
    }
    render(){
        const {contactInfo} = this.state;
        const contactInfoList = contactInfo.map( contactInfoItem => {
            return(
                <Col key={contactInfoItem.id} xs={6} className="mb-4">
                    <div>
                        <div className="my-4">
                            <i className={contactInfoItem.iconClassName} aria-hidden="true"></i>
                        </div>
                        <div>
                            <p>{contactInfoItem.numOne}</p>
                            <p>{contactInfoItem.numTwo}</p>
                        </div>
                    </div>
                </Col>
            )
        })
        return(
            <div className="main">
                <Container>
                    <Row>
                        <Col md={6}>
                            <div className="info">
                                <p>You can send us a message using this form in the left, (All fields with &nbsp;<span className="star">*</span>are required)</p>
                                <br/>
                                <p>Or you can call us or send email using these information below:</p>
                                <Row>
                                    {contactInfoList}
                                </Row>
                            </div>
                        </Col>
                        <Col md={6}>
                            <Form>
                                <Form.Group controlId="formBasicName">
                                    <Form.Label>Name<span className="star">*</span></Form.Label>
                                    <Form.Control type="text" />
                                </Form.Group>
                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control type="password" />
                                </Form.Group>
                                <Form.Group controlId="formBasicPassword">
                                    <Form.Label>Phone Number<span className="star">*</span></Form.Label>
                                    <Form.Control type="text" />
                                </Form.Group>
                                <Form.Group controlId="exampleForm.ControlTextarea1">
                                    <Form.Label>Message<span className="star">*</span></Form.Label>
                                    <Form.Control as="textarea" rows="4" />
                                </Form.Group>
                                <Button type="submit">
                                    Send
                                </Button>
                            </Form>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}
export default FormSection;