import React from 'react';
import {withRouter} from 'react-router-dom';
import {Card, Image} from 'react-bootstrap';

const ProductCard = ({title, text, imgUrl, linkUrl, history}) => {
    return(
    <div className="asLink" onClick={ () => history.push(`/${linkUrl}`)}>
        <Card className="text-center" style={{minHeight: "420px"}}>
            <Card.Body className='m-3 d-flex flex-column justify-content-between'>
                <div>
                    <Card.Title className='mb-5' style={{fontSize: "30px"}}>
                        {title}
                    </Card.Title>
                    <Card.Text>
                        {text}
                    </Card.Text>
                </div>
                <div>
                    <Image className='mt-2 CardImg' src={imgUrl} width='80%' height="170px" />
                </div>
            </Card.Body>
        </Card>
    </div>
)};
export default withRouter(ProductCard);