import React, {Fragment} from 'react';
import {Container, Col} from 'react-bootstrap';
import TypedReactDemo from '../Typed/Typed';
import {Link} from 'react-router-dom';

const MainSection = ({head, text, withTyped, link}) => {
    return(
        <div className='Main-Header'>
            <Container>
                <h2 className='my-4 head'>{head}</h2>
                {withTyped ? (
                    <Fragment>
                        <TypedReactDemo
                            strings={[
                            'PREMIUM QUALITY WOODS',
                            'FROM TREES TO YOU'
                            ]}
                        />
                        <Link to="/news"><i className="fa fa-chevron-right" aria-hidden="true"></i>{link}</Link>
                    </Fragment>
                ) : (
                <Col xs={8} style={{padding: "0px"}}>
                    <p style={{fontSize: "18px", letterSpacing: "4px", textTransform: "uppercase"}}>{text}</p>
                </Col>
            )}
            </Container>
        </div>
    )
}
export default MainSection;