import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Logo from '../../Images/Logo.png';
import {FooterIcons} from './footerData';

class Footer extends React.Component{
    constructor(){
        super();
        this.state = {
            footerIcons : FooterIcons
        }
    }
    render(){
        const {footerIcons} = this.state;
        const footerIconsList = footerIcons.map( ({id, className, link}) => {
            return(
                <a className="main-hover" key={id} href={link} target="_blank" rel="noopener noreferrer">
                    <i className={className} aria-hidden="true"></i>
                </a>
            )
        })
        return(
            <footer className='mt-5 mb-4'>
                <Container>
                    <Row className='d-flex align-items-center'>
                        <Col className='text-left d-flex flex-row justify-content-between-xs'>
                            {footerIconsList}
                        </Col>
                        <Col className='text-center'>
                            <img src={Logo} width="80px" alt="Logo"/>
                        </Col>
                        <Col className='text-right'>Designed by <span style={{color: '#d69162'}}>Omar</span></Col>
                    </Row>
                </Container>
            </footer>
        )
    }
}
export default Footer;