import React from "react";
import {Container, Row, Col} from 'react-bootstrap';
import { LightgalleryProvider } from "react-lightgallery";
import GalleryItem from '../../Components/GalleryItem/GalleryItem';
import FacebookCard from '../../Components/FacebookCard/FacebookCard';

const GROUP1 = [
    "https://firstwood.net/wp-content/uploads/IMG_0011-2-600x400.png",
    "https://firstwood.net/wp-content/uploads/IMG_0021-1-600x400.png",
    "https://firstwood.net/wp-content/uploads/IMG_0059-1-600x400.png",
    "https://firstwood.net/wp-content/uploads/DSC1216-600x401.png",
    "https://firstwood.net/wp-content/uploads/DSC1218-600x401.png",
];
const title = [
    "First Wood Working day",
    "First Wood Extension",
    "First Wood Poplar Dryer",
    "Peeling Process",
    "Drying Poplar",
]

const GallerySection = () => (
    <LightgalleryProvider>
        <Container className="my-5">
            <Row>
                {GROUP1.map((p, idx) => (
                    <GalleryItem key={idx} image={p} group="group1" title={title[idx]}  />
                ))}
                <Col lg={4} md={6}>
                    <FacebookCard />
                </Col>
            </Row>
        </Container>
    </LightgalleryProvider>
)

export default GallerySection;