import React from 'react';
import {Card} from 'react-bootstrap';

const TextCard = ({head, text, iconClass}) => (
    <Card className="mb-5 d-flex flex-column">
        {iconClass === "fa fa-quote-left fa-5x" ? (
        <div className="mt-5 ml-5 mb-3"> 
            <i className={iconClass} aria-hidden="true"/>
        </div>
        ) : null}    
        <Card.Body className='m-4'>
            <div className="h5 mb-4">{head}</div>
            <Card.Text className="mt-4">{text}</Card.Text>
        </Card.Body>
    </Card>
);
export default TextCard;