import React from 'react';
import MainSection from '../../Components/MainSection/MainSection';
import GallerySection from '../../Components/GallerySection/GallerySection';

class Gallery extends React.Component{
    constructor() {
        super();
        this.state = {
            head : "Gallery",
            text : "WELCOME TO OUR GALLERY, HERE YOU WILL FIND OUT HOW WE PRODUCE THE VENEER, PLYWOOD, BLOCK BOARD, WITH FINEST TRESS AND MATERIALS."
        }
    }
    render() {
        const {head, text} = this.state;
        return(
            <div>
                <MainSection head={head} text={text} />
                <GallerySection />
            </div>
        )
    }
}
export default Gallery;