import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import ProductCard from '../ProductCard/ProductCard';
import Pagination from "react-js-pagination";

class Paginations extends React.Component{
    constructor(props){
        super(props);
        this.state= {
            activePage : 1
        }
    }
    handlePageChange(pageNumber) {
        console.log(`active page is ${pageNumber}`);
        this.setState({activePage: pageNumber});
    }
    render(){
        const itemsCountPerPage = 9;
        const {activePage} = this.state;
        const {melaminesData} = this.props;

        // Get Current Item
        const indexOfLastPost = activePage * itemsCountPerPage;
        const indexOfFirstPost = indexOfLastPost - itemsCountPerPage;
        const currentItems = melaminesData.slice(indexOfFirstPost, indexOfLastPost);

        const ProductsList = currentItems.map (({id, ...data}) => {
            return(
                <Col lg={4} xs={6} key={id} className="mb-3">
                    <ProductCard {...data}/>
                </Col>
            )
        })
        return(
            <Container className="main pagination-container">
                <Row>
                    {ProductsList}
                </Row>
                <div className="mt-5 d-flex justify-content-center align-items-center">
                    <Pagination
                    itemClass="page-item"
                    linkClass="page-link"
                    activePage={activePage}
                    itemsCountPerPage={itemsCountPerPage}
                    totalItemsCount={melaminesData.length}
                    pageRangeDisplayed={3}
                    onChange={this.handlePageChange.bind(this)}
                    />
                </div>
            </Container>
        )
    }
}
export default Paginations;
