import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import ProductCard from '../ProductCard/ProductCard';

const SubProductsSection = ({subProducts}) => {

    const subProductsList = subProducts.map(({ id, ...otherSubProductsProps }) => {
        return(
            <Col key={id} lg={3} sm={6} className='mb-4 card-item'>
                <ProductCard {...otherSubProductsProps} />
            </Col>
        )
    })
    return (
    <Container className='product-menu my-5'>
        <Row>
            {subProductsList}
        </Row>
    </Container>
    );
}
export default SubProductsSection;