import React from 'react';
import {withRouter} from 'react-router-dom';
import MainSection from '../../Components/MainSection/MainSection';
import AboutSection from '../../Components/AboutSection/AboutSection';
import SubProductsSection from '../../Components/SubProductsSection/SubProductsSection';
import NewsSection from '../../Components/NewsSection/NewsSection';
import Paginations from '../../Components/Pagination/Pagination';

const SingleProduct = ({productsData, newsData, match}) => {

    const SingleData = productsData.find((item)=>(item.id === match.params.productId)),
        {title, text, aboutSectionHead, aboutSectionText, subProducts, melaminesData} = SingleData;
    return (
        <div>
            <MainSection head={title} text={text}/>
            {subProducts ? (
                <SubProductsSection subProducts={subProducts} />
            ): <Paginations melaminesData={melaminesData} />}
            
            <AboutSection sectionTitle={aboutSectionHead} sectionText={aboutSectionText} withImg/>
            <NewsSection newsData={newsData}/>
        </div>
    )
}
export default withRouter(SingleProduct);