import React from 'react';
import {withRouter} from 'react-router-dom';
import {Container,Row, Col} from 'react-bootstrap';
import MainSection from '../../Components/MainSection/MainSection';

const SingleNews = ({newsData, match}) => {

    const SingleData = newsData.find((item)=>(item.id === match.params.newsId)),
        {title,imgUrl, articleOne, articleTwo, articleThree} = SingleData;

    return(
        <div>
            <MainSection head={title} />
            <Container>
                <Row className="NewNested">
                    <Col lg={6}>
                        <div className="position-sticky text-center mb-4" style={{top: "50px"}}>
                            <img className="px-5" width="100%" height="400px" src={imgUrl} alt={title}/>
                        </div>
                    </Col>
                    <Col lg={6}>
                    <div className="pr-lg-5">
                        <p>{articleOne}</p>
                        <p>{articleTwo}</p>
                        <p>{articleThree}</p>
                    </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}
export default withRouter(SingleNews);
