import React from 'react';
import {withRouter} from 'react-router-dom';
import {Card} from 'react-bootstrap';

const NewsCard = ({ title, text, imgUrl, linkUrl, history }) => {
    return(
        <div className="asLink" onClick={ () => history.push(`/${linkUrl}`)}>
            <Card className="mb-5 d-flex flex-column overflow-hidden" style={{maxHeight:"458px"}}>
                <Card.Body className='m-3 d-flex flex-column'>
                    <div className="h5 mb-3">{title}</div>
                    <div>
                        <Card.Text>
                            {text}
                        </Card.Text>
                    </div>
                </Card.Body>
                <Card.Img className="mt-3" src={imgUrl} height="270px" width="100%"/>
            </Card>
        </div>
    );
} 
export default withRouter(NewsCard);
